// {"title":"Taking AngleSharp to the Next Level","date":"2018-03-09 10:15:00","categories":"news organization","author":"Florian Rappl","link":"/blog/2018-03-09-taking-anglesharp-to-the-next-level","source":"/home/runner/work/anglesharp.github.io/anglesharp.github.io/docs/blog/2018-03-09-taking-anglesharp-to-the-next-level.md"}
import * as React from 'react';
import { ContentPage } from 'piral-docs-tools/components';

      import { PageContent, Markdown, getPageLayout } from 'piral-docs-tools/components';

      const PageLayout = getPageLayout("blog");
      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2018-03-09-taking-anglesharp-to-the-next-level.md";
      const html = `
<p>In the recent two months some advancements have been made to ensure that AngleSharp will see active development also in the future. Today I am happy to announce that plans for joining the .NET Foundation are becoming more solid.</p>
<p>The current progress of this endeavour can be trakced <a href="https://github.com/AngleSharp/AngleSharp/issues/648">in the GitHub issue of the AngleSharp.Core repository</a>. Many steps are missing, but its a start.</p>
<p>Also to give an update on our current timetable. Currently, the roadmap looks as follows:</p>
<ul>
<li>A second hotfix for AngleSharp v0.9.9 will be released within the next (0.9.9.2); dealing with the ConditionalWeakTable (thanks for the PR!)</li>
<li>The big new v0.10 release should be out end of March / beginning of April (keep our fingers crossed) - together with it the first release of AngleSharp.Css is expected</li>
<li>AngleSharp.Scripting will see much dedication towards the middle of the year; expect to run Angular.js, React, Angular, any jQuery version etc. with it - we will try to make this all possible</li>
<li>Potentially towards EO Q3 2018 AngleSharp v0.11 will happen</li>
<li>Hopefully towards the end of the year we finally have v1</li>
</ul>
<p>Just some remarks on v0.11: Yes, the plan was to release v1 directly after v0.10, however, that plan is not realistic. First of all, there may be some issues that go beyond a hotfix (API changes). v0.10 has (unfortunately way too many) API changes, which need to be carefully assessed first. However, that alone does not justify a v0.11.</p>
<p>The most important aspect for v0.11 will be the drop of all legacy systems. Right now AngleSharp is delivered with multiple libraries inside the same NuGet package (one lib targetting .NET 4, another one for SL, PCL, ...). This will change - we will only target .NET Standard (either 1.0 or 1.3). The decision for the specific .NET Standard version is not yet done, but there are many arguments in favor of 1.3.</p>
<p>Ultimately, our goal is to make development of AngleSharp simpler and to provide a more robust and streamlined version to our users. Going for .NET Standard (1.3) is not only done for cosmetics, but involves an internal rewrite with <code>Span&lt;T&gt;</code> and <code>Memory&lt;T&gt;</code>. All in all we expect some serious performance improvements in parsing scenarios.</p>
`;
      const meta = {"title":"Taking AngleSharp to the Next Level","date":"2018-03-09 10:15:00","categories":"news organization","author":"Florian Rappl"};
    

export default () => (
  <ContentPage>
    
      <PageContent meta={meta}>
        <PageLayout meta={meta}>
          <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
        </PageLayout>
      </PageContent>
    
  </ContentPage>
);